.article-list,
.services-list {
  padding: 180px 15% 0px 15% !important;
  display: flex;
  flex-direction: column;

  @media (max-width: $breakpoin1024) {
    padding: 100px 5% 30px 5% !important;
  }

  h1 {
    @media (max-width: $breakpoin480) {
      font-size: calc(var(--mainFont) + 13px);
    }
  }

  a {
    height: 4em;
    margin-bottom: 50px;
    @media (max-width: $breakpoin480) {
      height: auto;
    }
  }
}

.article-inner {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: 180px 15% 30px 15%;
  margin-top: 180px;
  text-align: justify;
  @media (max-width: $breakpoin480) {
    margin: 180px 5% 30px 5%;
    font-size: calc(var(--mainFont) - 5px);
  }
  @media (max-width: 350px) {
    font-size: calc(var(--mainFont) - 8px);
  }

  h1 {
    @media (max-width: $breakpoin480) {
      font-size: calc(var(--mainFont) + 13px);
    }
    @media (max-width: 350px) {
      font-size: calc(var(--mainFont) + 10px);
    }
  }

  p {
    text-indent: 70px;
    margin-top: 0;
    @media (max-width: $breakpoin480) {
      text-indent: 20px;
    }
  }
  span {
    text-align: center;
  }

  a {
    height: auto;
    margin: 0;
  }

  ol {
    li {
      margin-bottom: 30px;
    }
  }
}
