.button{
  border: 3px solid $blueColor;
  border-radius: 15px;
  -webkit-transition: all 1s;
  -o-transition: all 1s;
  transition: all 1s;
  text-align: center;
  padding: 10px;
  text-decoration: none;
  color: black;
  display: flex;
    justify-content: center;
    align-items: center;

  &:hover {
    cursor: pointer;
    background-color: $blueColor;
    color: white;
  }
  &:visited {
    color: black;
  }

}

.flex-row-space-around{
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  justify-content: space-around;
  gap: 50px;

  @media (max-width: $breakpoin1024) { 
   flex-direction: column;
   justify-content: center;
   align-items: center;
  }
}

.center{
  text-align: center;
}

.hyper{
  display: inline-block;
  text-decoration: underline;
  border: none;
  border-radius: 0;
  transform: none;
  text-align:inherit;
  height: auto;
  width: auto;
  margin: 0;
  text-indent: 0;
  color: #0000FF;
  &:visited {
    color: #800080;
  }
  &:hover {
    cursor: pointer;
    background: transparent;
    color: #0000FF;
  }
}

.line-before{
  margin-top: 30px;
  margin-bottom: 10px;
  border-top: 3px solid lighten(#556270, 10%);
}