.module{
position: relative;
height: 100vh;
padding: 30px;
z-index: 5;
box-sizing: border-box;

.title {
  text-align: center;
  font-size: calc(var(--mainFont) + 20px);
  text-shadow: 5px 5px 5px $textShadow;
  pointer-events: none;
  @media (max-width: $breakpoin480) {
    font-size: var(--mainFont);
  }
}

  @media screen and (max-width: $breakpoin2560) {
    max-width: 2560px;}
  @media screen and (max-width: $breakpoin1920) {
    max-width: 1920px;}
  @media screen and (max-width: $breakpoin1440) {
    max-width: 1440px;}
  @media (max-width: $breakpoin1024) {
    max-width: 1024px;}
  @media (max-width: $breakpoin768) {
      max-width: 100%;}
  @media (max-width: $breakpoin480) {
      max-width: none;}
}