.swiper {
  width: 100%;
  height: 100%;
  margin: 0;

  .swiper-wrapper {
    -webkit-transition-duration: 1s !important;
    -o-transition-duration: 1s !important;
    transition-duration: 1s !important;

    .swiper-slide {
      padding-left: 100px;
      padding-right: 100px;
      -webkit-box-sizing: border-box;
      box-sizing: border-box;
      text-align: center;
      font-size: calc(var(--mainFont) + 10px);
      overflow: hidden;

      /* Center slide text vertically */
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-pack: center;
      -ms-flex-pack: center;
      justify-content: center;
      -webkit-box-align: start;
      -ms-flex-align: start;
      align-items: flex-start;
      -webkit-box-orient: vertical;
      -webkit-box-direction: normal;
      -ms-flex-direction: column;
      flex-direction: column;

      @media (max-width: $breakpoin480) {
        -webkit-box-align: center;
        -ms-flex-align: center;
        align-items: center;
      }

      .slideTitle {
        margin: 0;
        padding: 0;
        margin-bottom: 50px;
        position: absolute;
        top: 15%;
        left: 50%;
        -webkit-transform: translate(-50%);
        -ms-transform: translate(-50%);
        transform: translate(-50%);
        font-size: calc(var(--mainFont) + 15px);

        @media (max-width: $breakpoin1440) {
          width: 100%;
        }
        @media (max-width: $breakpoin1024) {
          font-size: var(--mainFont);
        }
        @media (max-width: $breakpoin480) {
          margin-bottom: 10px;
        }
        @media (max-width: $breakpoin360) {
          font-size: calc(var(--mainFont) - 7px);
        }
      }
      .slideInner {
        @media (max-width: $breakpoin1024) {
          font-size: calc(var(--mainFont) - 5px);
        }
        @media (max-width: $breakpoin480) {
          font-size: calc(var(--mainFont) - 10px);
        }
      }
      .firstInner {
        position: absolute;
        bottom: 10%;
        left: 50%;
        -webkit-transform: translate(-50%);
        -ms-transform: translate(-50%);
        transform: translate(-50%);
        width: 40%;
        z-index: 5;

        @media (max-width: $breakpoin1440) {
          width: 100%;
        }

        @media (max-width: $breakpoin1024) {
          font-size: calc(var(--mainFont) - 5px);
          bottom: 15%;
        }
        @media (max-width: $breakpoin480) {
          bottom: 10%;
        }
        @media (max-width: $breakpoin360) {
          font-size: calc(var(--mainFont) - 10px);
          bottom: 15%;
        }
      }
    }
  }

  .swiper-pagination-bullet {
    width: 25px;
    height: 25px;
    text-align: center;
    line-height: 20px;
    font-size: calc(var(--mainFont) - 18px);
    color: $blackColor;
    opacity: 1;
    background: $textShadow;
  }

  .swiper-pagination-bullet-active {
    color: $whiteColor;
    background: $blueColor;
  }

  .swiper-button-prev,
  .swiper-button-next {
    color: $blueColor;
  }
  .swiper-button-prev {
    left: 20px;
  }
  .swiper-button-next {
    right: 20px;
  }
  img {
    display: block;
    width: 100%;
    height: 100%;
    -o-object-fit: contain;
    object-fit: contain;
  }
}
