#slide-menu {
  position: fixed;

  -webkit-box-sizing: border-box;

          box-sizing: border-box;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  float: left;
  width: 100%;
  max-width: 284px;
  height: 100%;
  background: $grayBack;
  backdrop-filter: blur(5px);
  -webkit-backdrop-filter: blur(7px);
  z-index: 10;
  -webkit-transition: all 300ms;
  -o-transition: all 300ms;
  transition: all 300ms;

  @media (max-width: $breakpoin1024) {
    max-width: 200px;
    right: -200px;
  }
  @media (max-width: $breakpoin768) {
    max-width: 100vw;
    right: -100vw;
  }
  h2 {
    cursor: default;
    @media (max-width: $breakpoin1024) {
      font-size: calc(var(--mainFont) + 8px);
    }
  }

  ul {
    display: block;
    margin: 40px;
    padding: 0;
    list-style: none;
    opacity: 0.5;
    -webkit-transition: all 300ms;
    -o-transition: all 300ms;
    transition: all 300ms;

    li {
      padding: 6px 0;
      cursor: pointer;

      &:hover {
        color: $blueColor;
      }
    }

    li.sep {
      margin-top: 7px;
      padding-top: 14px;
      border-top: 1px solid lighten(#556270, 10%);
    }
  }

  .menu-trigger {
    position: absolute;
    bottom: 50px;
    left: -120px;
    width: 70px;
    height: 70px;
    background: #dfe5eb;
    cursor: pointer;
    border-radius: 5px;
    -webkit-transition: all 300ms;
    -o-transition: all 300ms;
    transition: all 300ms;
    border-radius: 20px;
    -webkit-animation: jumper 5s ease-in-out 2s infinite;
            animation: jumper 5s ease-in-out 2s infinite;
    &:hover {
      background: $blueColor;
    }
    img {
      border-radius: 20px;
      width: inherit;
      height: inherit;
    }

    @media (max-width: $breakpoin768) {
      bottom: 30px;
      height: 50px;
      width: 50px;
      left: -100px;
    }

    @media (max-width: $breakpoin480) {
      left: -100px;
    }

    @media (max-width: 320px) {
      left: -90px;
    }
  }
}


.menu-right{
  top: 0;
  right: -284px;
}



.menu-active {
  right: 0;
}

.jump-done {
  -webkit-animation: rotate 5s ease-in-out infinite !important;
          animation: rotate 5s ease-in-out infinite !important;
}

.active-trigger {
  -webkit-animation: none !important;
          animation: none !important;
  background: $blueColor !important;
  @media (max-width: $breakpoin768) {
    bottom: 30px !important;
    left: 87vw !important;
    z-index: 12;
  }
  @media (max-width: $breakpoin480) {
    left: 76.5vw !important;
  }
  @media (max-width: 320px) {
    left: 72vw !important;
  }
}