.header {
  position: fixed;
  top: 0;
  width: 100vw;
  height: 220px;
  color: $whiteColor;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: end;
  -ms-flex-pack: end;
  justify-content: flex-end;
  -webkit-box-align: start;
  -ms-flex-align: start;
  align-items: flex-start;
  overflow-x: hidden;
  z-index: 10;
  @media (max-width: $breakpoin1024) {
    height: 120px;
  }

  .line {
    position: absolute;
    width: 100vw;
    left: 0px;

    @media (max-width: $breakpoin1024) {
      top: -10px;
    }
    @media (max-width: $breakpoin768) {
      height: 70px;
    }
  }

  .logo {
    position: absolute;
    top: 20px;
    left: 2.5%;
    height: 200px;
    cursor: pointer;

    @media (max-width: $breakpoin1440) {
      height: 180px;
      left: 0%;
    }
    @media (max-width: $breakpoin1024) {
      top: 10px;
      height: 110px;
    }
    @media (max-width: $breakpoin768) {
      height: 100px;
    }

    @media (max-width: $breakpoin480) {
      height: 80px;
    }

    @media (max-width: $breakpoin360) {
      top: 0;
    }
  }

  .number {
    z-index: 10;
    position: fixed;
    top: 25px;
    right: 150px;
    cursor: default;
    @media (max-width: $breakpoin2560) {
      right: 300px;
    }
    @media (max-width: $breakpoin1920) {
      right: 150px;
    }
    @media (max-width: $breakpoin1440) {
      right: 100px;
    }
    @media (max-width: $breakpoin1024) {
      top: 20px;
      right: 50px;
    }
    @media (max-width: $breakpoin768) {
      display: none;
    }
    a {
      cursor: pointer;
      text-decoration: none;
      color: inherit;
    }
  }
  .telSmall {
    display: none;
    @media (max-width: $breakpoin768) {
      display: block;
      z-index: 10;
      position: fixed;
      cursor: pointer;
      text-decoration: none;
      color: $whiteColor;
      top: 12px;
      right: 80px;
    }
    @media (max-width: $breakpoin480) {
      right: 30px;
    }
    @media (max-width: 320px) {
      top: 8px;
      right: 25px;
    }
  }
}
